import { Component } from '@angular/core';
import { ModalController } from 'ionic-angular';
import { ViewProvider } from '../../providers/view/view';
import { PairModalContentPage, MotionModalContentPage } from '../../app/app.component';

@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html'
})
export class SettingsPage {

  constructor(public viewApi: ViewProvider, public modalCtrl: ModalController) {
  }

  pairNew() {
    console.log("opening pairing modal...");
    let modal = this.modalCtrl.create(PairModalContentPage, {});
    modal.present();
  }

  setupMotion() {
    console.log("opening motion modal...");
    let modal = this.modalCtrl.create(MotionModalContentPage, {});
    modal.present();
  }

  logout() {
    this.viewApi.logout();
  }

}



