import { Component } from '@angular/core';
import { ModalController, NavParams, ViewController, NavController, AlertController } from 'ionic-angular';
import { ViewProvider } from '../../providers/view/view';

@Component({
  selector: 'page-clips',
  templateUrl: 'clips.html'
})
export class ClipsPage {
  selectedItem: any;
  timelapse: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private viewApi: ViewProvider, public modalCtrl: ModalController, public alertCtrl: AlertController) {
    viewApi.timelapse.getClips();
    this.timelapse = viewApi.timelapse;
  }

  open(index) {
    console.log("index", index);
    let modal = this.modalCtrl.create(ClipsModalContentPage, {clipIndex: index});
    modal.present();
  }
  showConfirmDelete(clip) {
    let confirm = this.alertCtrl.create({
      title: 'Delete ' + clip.name + '?',
      message: 'This will remove preview thumbnails and data from the VIEW, but not delete any RAW image files',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
            console.log('Delete canceled');
          }
        },
        {
          text: 'DELETE',
          handler: () => {
            console.log('Deleting ' + clip.index);
            this.viewApi.timelapse.deleteClip(clip.index);
          }
        }
      ]
    });
    confirm.present();
  }
}


@Component({
  selector: 'page-clips',
  templateUrl: 'modal.html'
})
export class ClipsModalContentPage {
  clip:any;
  image:any;
  frames: any;
  private intervalHandle: any;

  constructor(
    public params: NavParams,
    public viewCtrl: ViewController,
    private viewApi: ViewProvider,
    public alertCtrl: AlertController
  ) {
    console.log("clipIndex", this.params.get('clipIndex'));
    this.clip = viewApi.timelapse.clips[this.params.get('clipIndex')];
    this.image = this.clip.image;
    this.frames = this.viewApi.timelapse.frames;
    this.clip.frameIndex = 0;
    if(!this.clip.info) this.viewApi.timelapse.getClipInfo(this.clip.name);
    if(!this.viewApi.timelapse.frames[this.clip.index] && !this.clip.loadingFrames) this.viewApi.timelapse.getClipFrames(this.clip.index);
  }

  startFrame(clip) {
    if(this.viewApi.timelapse.frames[clip.index]) {
      if(clip.playing) this.togglePlay(clip);
      clip.frameIndex = 0;
      //this.image = this.viewApi.timelapse.frames[this.clip.index][clip.frameIndex];
    }
  }

  endFrame(clip) {
    if(this.viewApi.timelapse.frames[clip.index]) {
      if(clip.playing) this.togglePlay(clip);
      clip.frameIndex = this.viewApi.timelapse.frames[this.clip.index].length - 1;
      //this.image = this.viewApi.timelapse.frames[this.clip.index][clip.frameIndex];
    }
  }

  previousFrame(clip) {
    if(this.viewApi.timelapse.frames[clip.index]) {
      if(clip.playing) this.togglePlay(clip);
      if(clip.frameIndex > 0) {
        clip.frameIndex--;
        //this.image = this.viewApi.timelapse.frames[this.clip.index][clip.frameIndex];
      }
    }
  }

  nextFrame(clip) {
    if(this.viewApi.timelapse.frames[clip.index]) {
      if(clip.playing) this.togglePlay(clip);
      if(clip.frameIndex < this.viewApi.timelapse.frames[this.clip.index].length - 1) {
        clip.frameIndex++;
        //this.image = this.viewApi.timelapse.frames[this.clip.index][clip.frameIndex];
      }
    }
  }

  updateFrame(clip, event) {
    if(this.viewApi.timelapse.frames[clip.index]) {
      //if(clip.playing) this.togglePlay(clip);
      if(clip.frameIndex >= 0 && clip.frameIndex < this.viewApi.timelapse.frames[this.clip.index].length) {
        this.image = this.viewApi.timelapse.frames[this.clip.index][clip.frameIndex];
      }
    }
  }

  togglePlay(clip) {
    if(this.viewApi.timelapse.frames[clip.index]) {
      if(clip.playing) {
        clearInterval(this.intervalHandle);
        clip.playing = false;
      } else {
        clip.playing = true;
        if(clip.frameIndex >= this.viewApi.timelapse.frames[this.clip.index].length -1) clip.frameIndex = 0;
        this.intervalHandle = setInterval(() => {
          //this.image = this.viewApi.timelapse.frames[this.clip.index][clip.frameIndex];
          if(clip.frameIndex >= this.viewApi.timelapse.frames[this.clip.index].length - 1) return this.togglePlay(clip);
          clip.frameIndex++;
        }, 1000/30);
      }
    }
  }

  logReport(clip) {
    let reportPrompt = this.alertCtrl.create({
      title: 'Send report for ' + clip.name,
        buttons: [
        {
          text: 'Cancel',
          handler: () => {
            console.log('Report canceled');
          }
        },
        {
          text: 'SEND',
          handler: (data:any) => {
            console.log('Sending report for ' + clip.index);
            this.viewApi.timelapse.sendReport(clip.name, (title, message) => {
              let res = this.alertCtrl.create({
                title: title,
                message: message,
                buttons: [
                  {
                    text: 'OK',
                  }
                ]
              });
              res.present();
            });
          }
        }
      ]
    });
    reportPrompt.present();
  }


  dismiss() {
    this.viewCtrl.dismiss();
  }
}


