import { Component, ViewChild } from '@angular/core';
import { Nav, NavParams, Platform, Events, ModalController, ViewController, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ToastController } from 'ionic-angular';

import { CameraPage } from '../pages/camera/camera';
import { IntervalometerPage } from '../pages/intervalometer/intervalometer';
import { ClipsPage } from '../pages/clips/clips';
import { SettingsPage } from '../pages/settings/settings';

import { ViewProvider } from '../providers/view/view';

@Component({
  templateUrl: 'app.html'
})
export class ViewApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = CameraPage;

  pages: Array<{title: string, component: any, icon: string}>;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen, public events: Events, private viewApi: ViewProvider, public modalCtrl: ModalController, public alertCtrl: AlertController, public toastCtrl: ToastController) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Camera', component: CameraPage, icon: 'camera' },
      { title: 'Intervalometer', component: IntervalometerPage, icon: 'timer' },
      { title: 'Time-lapse Clips', component: ClipsPage, icon: 'film' },
      { title: 'Settings', component: SettingsPage, icon: 'settings' }
    ];

    let alert: any = null;
    events.subscribe('error', (err) => {
      let wait = 0;
      if(alert) {
        alert.dismiss();
        alert = null;
        wait = 500;
      }
      setTimeout(() => {
        alert = this.alertCtrl.create({
          title: 'ERROR',
          subTitle: err.message || err.msg || 'unknown error',
          buttons: ['Close']
        });
        alert.present();
      }, wait);
    });

    let toast: any = null;
    let newToast: any = null;
    let autoDismissedNoDevice: boolean = true;
    events.subscribe('login_required', () => {
      console.log('login_required event captured');
      let modal = this.modalCtrl.create(LoginModalContentPage, {});
      if(toast) {
        autoDismissedNoDevice = true;
        toast.dismiss();
        toast = null;
      }
      modal.present();
    });

    events.subscribe('disconnected', (msg) => {
      let wait = 0;
      if(toast) {
        autoDismissedNoDevice = true;
        toast.dismiss();
        toast = null;
        wait = 1000;
      }
      newToast = this.toastCtrl.create({
        message: msg,
        position: 'bottom',
        cssClass: 'error-toast'
      });
      setTimeout(() => {
        newToast.present();
        toast = newToast;
      }, wait);
    });

    events.subscribe('connected', (msg) => {
      let wait = 0;
      if(toast) {
        autoDismissedNoDevice = true;
        toast.dismiss();
        toast = null;
        wait = 1000;
      }
      newToast = this.toastCtrl.create({
        message: msg,
        position: 'bottom',
        duration: 3000,
        //showCloseButton: true,
      });
      setTimeout(() => {
        newToast.present();
        toast = newToast;
      }, wait);
    });

    events.subscribe('nodevice', (status) => {
      let wait = 0;
      if(toast) {
        autoDismissedNoDevice = true;
        toast.dismiss();
        toast = null;
        wait = 1000;
      }
      if(!status) return; // device added

      newToast = this.toastCtrl.create({
        message: "No VIEW device connected.  Connect a paired device to wifi to access or pair a new device.",
        position: 'bottom',
        showCloseButton: true,
        closeButtonText: 'Add new VIEW'
      });

      setTimeout(() => {
        newToast.onDidDismiss(() => {
          if(this.viewApi.noDevice && this.viewApi.connected && this.viewApi.loggedIn) {
            console.log('Adding VIEW device...');
            let modal = this.modalCtrl.create(PairModalContentPage, {});
            modal.present();
          }
        });
        autoDismissedNoDevice = false;
        newToast.present();
        toast = newToast;
      }, wait);
    });

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}

@Component({
  templateUrl: 'login.modal.html'
})
export class LoginModalContentPage {

  constructor(
    public viewCtrl: ViewController,
    private viewApi: ViewProvider,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController
  ) {

  }

  submit(mode, name, email, password, password1, password2) {
    if(mode == 'register') {
      this.register(name, email, password1, password2);
      return false;
    } else {
      this.login(email, password);
      return false;
    }
  }

  register(name, email, password1, password2) {
    let toast:any = this.toastCtrl.create({
      message: "Creating account for " + email + "...",
      position: 'bottom',
    });
    if(password1 && password1.length > 5) {
      if(password1 == password2) {
        toast.present();
        this.viewApi.register(email, name, password1).then((res:any) => {
          if(toast) {
            console.log("dismissing toast...");
            toast.dismiss();
          }
          console.log("registration res", res);
          if(res.action == 'login') {
            this.viewCtrl.dismiss();
          } else if(res.action == 'login_failed') {
            console.log("message:", res.message);
            toast = this.toastCtrl.create({
              message: res.message,
              position: 'bottom',
              cssClass: 'error-toast',
              duration: 5000,
            });
            toast.present();
          }
        }, ()=> {
          if(toast) {
            toast.dismiss();
          }
          toast = this.toastCtrl.create({
            message: "Error contacting server - please try again later",
            position: 'bottom',
            cssClass: 'error-toast',
            showCloseButton: true,
          });
          toast.present();
        });  
      } else {
        toast = this.toastCtrl.create({
          message: "Passwords do not match",
          position: 'bottom',
          cssClass: 'error-toast',
          duration: 5000,
          showCloseButton: true,
        });
        toast.present();
      }
    } else {
      toast = this.toastCtrl.create({
        message: "Password must be at least 5 characters long",
        position: 'bottom',
        cssClass: 'error-toast',
        duration: 5000,
        showCloseButton: true,
      });        
      toast.present();
    }
  }

  login(email, password) {
    let toast:any = this.toastCtrl.create({
      message: "Logging in as " + email + "...",
      position: 'bottom',
    });
    toast.present();
    this.viewApi.login(email, password).then((res:any) => {
      if(toast) {
        console.log("dismissing toast...");
        toast.dismiss();
      }
      console.log("login res", res);
      if(res.action == 'login') {
        this.viewCtrl.dismiss();
      } else if(res.action == 'login_failed') {
        console.log("message:", res.message);
        toast = this.toastCtrl.create({
          message: res.message,
          position: 'bottom',
          cssClass: 'error-toast',
          duration: 5000,
        });
        toast.present();
      }
    }, ()=> {
      if(toast) {
        toast.dismiss();
      }
      toast = this.toastCtrl.create({
        message: "Error contacting server - please try again later",
        position: 'bottom',
        cssClass: 'error-toast',
        showCloseButton: true,
      });
      toast.present();
    });  
  }

  resetPasswordPrompt(email) {
    let alert = this.alertCtrl.create();
    alert.setTitle("Account Email");

    alert.addInput({
      placeholder: 'email address',
      value: email,
      type: 'email',
    });

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: (data: any) => {
        this.doPasswordReset(data[0]);
      }
    });
    alert.present();
  }

  showResetDialog(email) {
    let toast:any = null;
    let alert = this.alertCtrl.create();
    alert.setTitle("Reset password (code sent to email)");

    alert.addInput({
      placeholder: 'code (from email)',
      type: 'number',
    });
    alert.addInput({
      placeholder: 'new password',
      type: 'password',
    });
    alert.addInput({
      placeholder: 'confirm password',
      type: 'password',
    });

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: (data: any) => {
        let code = data[0];
        let password1 = data[1];
        let password2 = data[2];
        if(password1 != password2) {
          toast = this.toastCtrl.create({
            message: "Error: passwords do not match",
            position: 'bottom',
            cssClass: 'error-toast',
            duration: 5000,
          });
          toast.present();
          this.showResetDialog(email);
        } else {
          toast = this.toastCtrl.create({
            message: "Resetting password...",
            position: 'bottom',
          });
          this.viewApi.resetPassword(email, code, password1).then((res:any) => {
            if(toast) {
              toast.dismiss();
            }
            if(res.action == 'login') {
              toast = this.toastCtrl.create({
                message: "Password reset, logged in!",
                duration: 5000,
                showCloseButton: true,
                position: 'bottom',
              });
              this.viewCtrl.dismiss();
            } else if(res.action == 'register') {
              console.log("message:", res.message);
              toast = this.toastCtrl.create({
                message: res.message,
                position: 'bottom',
                cssClass: 'error-toast',
                duration: 5000,
              });
              toast.present();
            }
          }, ()=> {
            if(toast) {
              toast.dismiss();
            }
            toast = this.toastCtrl.create({
              message: "Error contacting server - please try again later",
              position: 'bottom',
              cssClass: 'error-toast',
              showCloseButton: true,
            });
            toast.present();
          });  
        }
      }
    });
    alert.present();
  }

  doPasswordReset(email) {
    let toast:any = this.toastCtrl.create({
      message: "Checking account for " + email + "...",
      position: 'bottom',
    });
    toast.present();
    this.viewApi.resetPasswordRequest(email).then((res:any) => {
      if(toast) {
        toast.dismiss();
      }
      console.log("login res", res);
      if(res.action == 'reset') {
        this.showResetDialog(email);
      } else if(res.action == 'register') {
        console.log("message:", res.message);
        toast = this.toastCtrl.create({
          message: res.message,
          position: 'bottom',
          cssClass: 'error-toast',
          duration: 5000,
        });
        toast.present();
      }
    }, ()=> {
      if(toast) {
        toast.dismiss();
      }
      toast = this.toastCtrl.create({
        message: "Error contacting server - please try again later",
        position: 'bottom',
        cssClass: 'error-toast',
        showCloseButton: true,
      });
      toast.present();
    });  
  }
}

@Component({
  templateUrl: 'pair.modal.html'
})
export class PairModalContentPage {

  toast:any = null;

  constructor(
    public viewCtrl: ViewController,
    private viewApi: ViewProvider,
    private toastCtrl: ToastController
  ) {
    console.log("PairModalContentPage initialized")
  }
  
  dismiss() {
    if(this.toast) {
      this.toast.dismiss();
    }
    this.viewApi.noDevice = false;
    this.viewCtrl.dismiss();
  }

  pair(code) {
    this.toast = this.toastCtrl.create({
      message: "Pairing device...",
      position: 'bottom',
    });
    this.toast.present();
    this.viewApi.addDevice(code).then((res:any) => {
      if(this.toast) {
        this.toast.dismiss();
      }
      console.log("login res", res);
      if(res.action == 'device_added') {
        this.viewCtrl.dismiss();
      } else {
        console.log("message:", res.message);
        this.toast = this.toastCtrl.create({
          message: res.message,
          position: 'bottom',
          cssClass: 'error-toast',
          duration: 10000,
        });
        this.toast.present();
      }
    }, ()=> {
      if(this.toast) {
        this.toast.dismiss();
      }
      this.toast = this.toastCtrl.create({
        message: "Error contacting server - please try again later",
        position: 'bottom',
        cssClass: 'error-toast',
        showCloseButton: true,
      });
      this.toast.present();
    });  
  }
}

@Component({
  templateUrl: 'motion.modal.html'
})
export class MotionModalContentPage {
  axes: any = [];
  motion: any = {};
  toast:any = null;
  axisId:any = null;

  constructor(
    public params: NavParams,
    public viewCtrl: ViewController,
    private viewApi: ViewProvider,
    private events: Events
  ) {
    this.axisId = params.get('axisId');
    if(this.axisId) {
      this.axes = viewApi.motion.axis.filter(a => { return a.id == this.axisId});
    } else {
      this.motion = viewApi.motion;
      this.motion.get();
      this.events.subscribe('motion.updated', () => {
        this.axes = viewApi.motion.axis || [];
        this.motion = viewApi.motion;
      })
    }

    console.log("MotionModalContentPage initialized", this.axes);
  }
  
  dismiss() {
    for(let i = 0; i < this.axes.length; i++) {
      this.updateHardware(this.axes[i]);
      this.updateType(this.axes[i]);
      this.axes[i].setup = true;
      if(this.axes[i].unit == 's') this.axes[i].unitSteps = 1;
      this.viewApi.db.set('motion-' + this.axes[i].id, this.axes[i]);
      console.log("saving motion setup:", this.axes[i])
      this.motion.axis = this.axes;
    }

    if(this.toast) {
      this.toast.dismiss();
    }
    this.viewApi.noDevice = false;
    this.viewCtrl.dismiss();
  }

  updateHardware(axis) {
      console.log("updating hardware", axis)
      if(axis.driver == 'NMX' && axis.hardware == 'stager') {
          axis.unitSteps = 558.63333334;
      } else if(axis.driver == 'NMX' && axis.hardware == 'sapphire') {
          axis.unitSteps = 550.81967213;
      }
  }
  updateType(axis) {
      console.log("updating driver", axis)
      if(axis.name == 'Pan' || axis.name == 'Tilt') {
          axis.unit = '°';
          if(axis.driver == 'NMX') {
              if(axis.hardware == 'stager') {
                  axis.unitSteps = 558.63333334;
              } else if(axis.hardware == 'sapphire') {
                  axis.unitSteps = 550.81967213;
              }
          } else if(axis.driver == 'GM') {
              axis.unitSteps = 1;
          }
      } else {
          axis.unit = 's';
          axis.unitSteps = 1;
      }
  }
}




