import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ModalController, AlertController } from 'ionic-angular';

import { ViewProvider } from '../../providers/view/view';
import { MotionModalContentPage } from '../../app/app.component';


@Component({
  selector: 'page-camera',
  templateUrl: 'camera.html'
})
export class CameraPage {
  public camera:any = {};
  public motion:any = {};
  public intervalometer:any = {};
  public viewMode = 'camera';



  constructor(public navCtrl: NavController, private alertCtrl: AlertController, private viewApi: ViewProvider, public modalCtrl: ModalController) {
  	this.camera = this.viewApi.camera;
  	this.motion = this.viewApi.motion;
  	this.intervalometer = this.viewApi.intervalometer;
  	this.motion.get();
  }

  setupMotion(axisId) {
    let modal = this.modalCtrl.create(MotionModalContentPage, {axisId: axisId});
    modal.present();
  }

  zeroMotion(axisId) {
    this.motion.setAxisPosition(axisId, 0)
  }

  doSelect(list, model, title, set) {
    let alert = this.alertCtrl.create();
    alert.setTitle(title || "select an option");

    for(let i = 0; i < list.length; i++) {
    	let item = list[i];
	    alert.addInput({
	      type: 'radio',
	      label: item.label || item.title || item,
	      value: item,
	      checked: ((item.value || item.title || item)  == (model.value || model.title || model))
	    });
    }

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: (data: any) => {
        console.log('Radio data:', data);
        for(let key in model) {
        	model[key] = data[key];
        }
        if(set) {
        	console.log("setting", set, "to", data.value);
		    this.camera.set(set, data.value);
        }
      }
    });

    alert.present();
  }

}



