import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule, Injectable, Injector } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpClientModule } from '@angular/common/http';
import { IonicStorageModule } from '@ionic/storage';

import { ViewApp, LoginModalContentPage, PairModalContentPage, MotionModalContentPage } from './app.component';
import { CameraPage } from '../pages/camera/camera';
import { IntervalometerPage, KeyframeModalContentPage, UpdateModalContentPage, RampingModalContentPage } from '../pages/intervalometer/intervalometer';
import { ClipsPage, ClipsModalContentPage } from '../pages/clips/clips';
import { SettingsPage } from '../pages/settings/settings';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { Pro } from '@ionic/pro';

//import {TimeAgoPipe} from 'time-ago-pipe';
import { TimeAgoPipeModule } from "time-ago-pipe/index";

import { ViewProvider } from '../providers/view/view';

const IonicPro = Pro.init('5d0a65db', {
  appVersion: "0.0.1"
});

@Injectable()
export class MyErrorHandler implements ErrorHandler {
  ionicErrorHandler: IonicErrorHandler;

  constructor(injector: Injector) {
    try {
      this.ionicErrorHandler = injector.get(IonicErrorHandler);
    } catch(e) {
      // Unable to get the IonicErrorHandler provider, ensure 
      // IonicErrorHandler has been added to the providers list below
    }
  }

  handleError(err: any): void {
    IonicPro.monitoring.handleNewError(err);
    // Remove this if you want to disable Ionic's auto exception handling
    // in development mode.
    this.ionicErrorHandler && this.ionicErrorHandler.handleError(err);
  }
}

@NgModule({
  declarations: [
    ViewApp,
    CameraPage,
    IntervalometerPage,
    ClipsPage,
    SettingsPage,
    ClipsModalContentPage,
    LoginModalContentPage,
    PairModalContentPage,
    MotionModalContentPage,
    KeyframeModalContentPage,
    UpdateModalContentPage,
    RampingModalContentPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(ViewApp),
    IonicStorageModule.forRoot(),
    TimeAgoPipeModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    ViewApp,
    CameraPage,
    IntervalometerPage,
    ClipsPage,
    SettingsPage,
    ClipsModalContentPage,
    LoginModalContentPage,
    PairModalContentPage,
    MotionModalContentPage,
    KeyframeModalContentPage,
    UpdateModalContentPage,
    RampingModalContentPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ViewProvider,
    HttpClientModule,
    IonicErrorHandler,
    [{ provide: ErrorHandler, useClass: MyErrorHandler }]
  ]
})
export class AppModule {}



